<template>
	<view class="flex flex-direction">
		<!-- 是否登录 -->
		<is-login currentPage="index"></is-login>
		<!-- 标题栏 -->
		<cu-custom bgColor="bg-cyan" :isBack="preview">
			<block slot="content">组织信息</block>
		</cu-custom>
		<uni-list>
			<uni-list-item
				:thumb="(current.Logo == null || current.Logo == '' || current.Logo == 'string') ? '/static/organ/organ_default.png': current.Logo"
				thumbSize="lg" clickable @click="changeLogo">
				<template slot="body">
					<view class=" cf ">
						<view class="fl text-lg text-bold padding-top-xs">
							{{ current.Name }}
						</view>
						<view class="text-sm fr cu-tag bg-cyan light margin-left text-cut">
							{{ current.ParentId ? '分店' : '组织' }}
						</view>
					</view>
					<view class="text-bold text-cut text-lg"></view>
					<view class="text-sm text-cut text-grey">联系方式：{{ current.LinkTel }}</view>
				</template>
			</uni-list-item>
		</uni-list>
		<!-- 是否包含当前分店 -->
		<template>
			<navigator v-if="current.MchId != current.TopMchId"
				:url="'/pages/basic/orgation/orgationdetail?preview=true&MchId=' +  current.TopMchId">
				<view class="cu-form-group">
					<view class="title text-bold">所属组织</view>
					<view class="text-grey">{{ TopMchName }}
						<text class=" cuIcon-right margin-left"></text>
					</view>
				</view>
			</navigator>
			<view class="cu-form-group" @tap="showDrawer">
				<view class="title text-bold">所含分店</view>
				<text class="text-grey cuIcon-right"></text>
			</view>
		</template>
		<form>
			<!-- 所属组织 -->
			<view v-if="current.TopMchId" class="cu-form-group margin-top-sm">
				<view class="title text-bold">所属组织</view>
				<text class="text-grey">{{ current.ParentName ? current.ParentName : '无' }}</text>
			</view>
			<!-- 组织机构代码 -->
			<view class="cu-form-group " :class="current.TopMchId ? '':'margin-top'">
				<view class="title text-bold">组织代码</view>
				<text class="text-grey">{{ current.MchNo ? current.MchNo : '' }}</text>
			</view>
			<!-- 名称 -->
			<view class="cu-form-group ">
				<view class="title text-bold">名称</view>
				<text class="text-grey">{{current.Name ? current.Name : ''}}</text>
			</view>
			<!-- 描述 -->
			<view class="cu-form-group ">
				<view class="title text-bold">描述</view>
				<textarea maxlength="-1" class="text-grey" auto-height
					:value="current.Msg? current.Msg : ''"></textarea>
			</view>
			<!-- 行业 -->
			<view class="cu-form-group ">
				<view class="title text-bold">行业</view>
				<text class="text-grey">{{current.IndustryName ? current.IndustryName : ''}}</text>
			</view>
			<!-- 邮编 -->
			<view class="cu-form-group ">
				<view class="title text-bold">邮编</view>
				<text class="text-grey">{{current.PostCode ? current.PostCode : ''}}</text>
			</view>
			<!-- 地区 -->
			<view class="cu-form-group ">
				<view class="title text-bold">地区</view>
				<text
					class="text-grey">{{current.Area1Name ? current.Area1Name : ''}}&nbsp;{{current.Area2Name ? current.Area2Name :''}}&nbsp;{{current.Area3Name ? current.Area3Name :''}}</text>
			</view>
			<!-- 地址 -->
			<view class="cu-form-group ">
				<view class="title text-bold">地址</view>
				<text class="text-grey">{{current.Address ? current.Address :''}}</text>
			</view>
			<!-- 法人 -->
			<view class="cu-form-group margin-top-sm">
				<view class="title text-bold">法人</view>
				<text class="text-grey">{{current.LegalPerson ? current.LegalPerson : ''}}</text>
			</view>
			<!-- 开户行 -->
			<view class="cu-form-group ">
				<view class="title text-bold">开户行</view>
				<text class="text-grey">{{current.BankName ? current.BankName : ''}}</text>
			</view>
			<!-- 开户账号 -->
			<view class="cu-form-group ">
				<view class="title text-bold">开户账号</view>
				<text class="text-grey">{{current.BankAccount ? current.BankAccount : ''}}</text>
			</view>
			<!-- 状态 -->
			<view class="cu-form-group ">
				<view class="title text-bold">状态</view>
				<text class="text-grey">{{current.Status ? current.Status : ''}}</text>
			</view>
			<!-- 负责人 -->
			<view class="cu-form-group ">
				<view class="title text-bold">负责人</view>
				<text class="text-grey">{{current.Managers ? current.Managers[0] : '无'}}</text>
			</view>
			<!-- 联系人 -->
			<view class="cu-form-group ">
				<view class="title text-bold">联系人</view>
				<text
					class="text-grey">{{ current.LinkMan ? current.LinkMan :'' }}({{current.LinkTel ? current.LinkTel : ''}})</text>
			</view>
		</form>
		<!-- 编辑和删除页 -->
		<view class="margin padding round cu-btn bg-cyan" @click="edit">保存</view>
		<view class="margin-lr padding round cu-btn bg-red" @click="deleteClick">删除</view>
		<!-- 抽屉 跳转到对应的门店-->
		<uni-drawer ref="showRight" mode="right" :mask-click="false" @tap="closeDrawer" style="top: 65px;" >
			<scroll-view style="height: 100vh;padding-top:65px"  scroll-y="true" >
				<uni-list>
					<uni-list-item showArrow :title="item.Name + '(' + item.MchNo + ')'" v-for="(item , index) in organization_list" :key="index"
					clickable @click="toSonMerchant"></uni-list-item>
				</uni-list>
			</scroll-view>
		</uni-drawer>
	</view>
</template>

<script>
	/**
	 * 查看组织信息
	 * @author 陆彦捷
	 * @date 2021.07.01 查看当前组织
	 * @date 2021.07.23 重新引用global
	 * @description 查看组织以及所含门店信息
	 */
	import organ from '@/common/storage/organstorage.js'
	var _self;
	export default {
		data() {
			return {
				// 页面相关消息
				preview: false, // 是否返回上一页
				canEdit : true ,	// 根据实际，查询是否有编辑组织的权利
				// 系统相关消息
				Token: this.global.login.getToken(), // 当前Token
				MchId: 0, // 组织id
				current: null, // 当前显示的组织或分店信息
				mainMch: {}, // 主企业信息
				TopMchName: '', // 顶级组织名称
				orgIndex: 1, // 查看分店信息的页码（默认为1）
				orgPageSize : 10 ,	// 默认每页条数（10）
				loadding_list: false, // 分店信息是否查询中
				organization_list: [], // 当前企业所含门店的信息
			}
		},
		onLoad: function(options) {
			_self = this;
			this.getParams(options);
			this.request_Org(this.MchId); // 查询当前组织
			this.request_partOrg(this.MchId);	// 查询当前组织的分店
		},
		methods: {
			// ======================================= 初始化方法 ==================================================
			/** 获取参考值
			 * @param {Object} options
			 */
			getParams: function(options) {
				this.preview = options.preview != 'false' ? Boolean(options.preview) : false; // 是否可返回上一页
				this.MchId = options.MchId ? options.MchId : organ.getIndustryId(); // 组织id
			},
			// ======================================= 监听事件 ==================================================
			/** 修改头像
			 * @param {Object} e
			 */
			changeLogo : function(e){
				uni.showToast({
					title: '假装修改头像成功！'
				});
			},
			/**
			 *  打开右侧抽屉
			 */
			showDrawer : function() {
                this.$refs.showRight.open();
            },
			/**
			 * 关闭右侧抽屉
			 */
            closeDrawer :function() {
                this.$refs.showRight.close();
            },
			/** 保存当前门店信息，并跳转到子门店
			 * @param {Object} e
			 */
			toSonMerchant : function(e){
				uni.showModal({
					title:'跳转',
					content:'将查看分店信息，是否保存当前信息？',
					success:function(result){
						if(result.confirm){
							_self.edit();
						}
					},
					complete:function(){
						uni.showToast({
							title: '假装跳转到分店'
						});
					}
				});
			},
			/** 修改当前组织信息
			 * @param {Object} e
			 */
			edit :function (e){
				uni.showToast({
					title:'假装修改组织信息成功！'
				});
			},
			/** 删除当前组织信息
			 * @param {Object} e
			 */
			deleteClick : function(e){
				uni.showModal({
					title:'删除',
					content:'确认删除当前组织信息？',
					success:function(result){
						if(result.confirm){
							_self.deleteMch_request(0);
						}
					}
				});
			},
			// ======================================= 请求方法 ==================================================
			/**
			 * 根据id查看组织信息
			 * @param {Number} MchId 组织id
			 */
			request_Org(MchId) {
				uni.showLoading();
				uni.request({
					url: _self.website + '/api/SvcMerchant/Info/' + MchId,
					header: {
						Authorization: 'Bearer ' + _self.Token
					},
					method: 'GET',
					success: function(result) {
						uni.hideLoading();
						if (result.statusCode == 200) {
							if (result.data.ResultCode == "0") {
								_self.current = result.data.Data;
								if(_self.current.TopMchId != _self.current.MchId && _self.current.TopMchId != 0){
									_self.topMch_request(_self.current.TopMchId);
								}else{
									_self.TopMchName = '无';
								}
								return;
							}
						}
					},
					fail: function(erros) {
						uni.hideLoading();
						console.log("fail", erros);
					}
				});
			},
			/** 查询顶级组织基本信息(用于查询所属组织)
			 * @param {Object} TopMchId
			 */
			topMch_request: function(TopMchId) {
				uni.request({
					url: _self.website + '/api/SvcMerchant/Info/' + TopMchId,
					header: {
						Authorization: 'Bearer ' + _self.Token
					},
					method: 'GET',
					success: function(result) {
						if (result.statusCode == 200) {
							if (result.data.ResultCode == "0") {
								_self.TopMchName = result.data.Data.Name;
								return;
							}
						}
					},
					fail: function(erros) {}
				});
			},
			/** 查看当前组织所包含的分店
			 * @param {Object} MchId 组织Id
			 */
			request_partOrg: function(MchId) {
				this.loadding_list = true;
				uni.request({
					url: _self.website + '/api/SvcMerchant/Page?topMchId=' + MchId + '&pageIndex=' + _self.orgIndex +'&pageSize=' + _self.orgPageSize,
					header: {
						Authorization: 'Bearer ' + _self.Token
					},
					method: 'GET',
					success: function(result) {
						_self.loadding_list = false;
						if (result.statusCode == 200) {
							if (result.data.ResultCode == "0") {
								var data = result.data.Data;
								_self.organization_list = _self.organization_list.concat(data);
								_self.orgIndex ++ ;
								return;
							}
						}
					},
					fail: function(erros) {
						_self.loadding_list = false;
						// uni.hideLoading();
						// console.log("fail", erros);
					}
				})
			},
			/** 删除当前组织请求
			 * @param {Object} MchId 组织Id
			 */
			deleteMch_request : function(MchId){
				uni.showToast({
					title:'假装删除当前组织id成功',
					icon:'none'
				});
			},
		// ===============================================================================================================
		}
	}
</script>

<style>
	.page{
		height: calc(100vh -65px);
	}
</style>
